import CharacterCard from '@/components/CharacterCard'
import { getCharacterByBane } from '@/lib/api'
import React from 'react'

const SearchPage = async ({ searchParams }: { searchParams: Promise<{ query: string }> }) => {
  const query = (await searchParams).query
  if (!query) return null
  const charactersData = await getCharacterByBane(query)
  console.log(charactersData[0])
  return (
    <div>
      <h1 className='text-center text-4xl text-red-500'>
        <span className='text-yellow-400'>Search for:</span> {query} ({charactersData.length}{' '}
        {charactersData.length === 1 ? 'result' : 'results'})
      </h1>
      <div className='mt-20 w-full grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-8'>
        {charactersData.map((character: any) => (
          <CharacterCard key={character.id} character={character} />
        ))}
      </div>
    </div>
  )
}

export default SearchPage
